@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500&display=swap);
.movie {
    position: relative;
    display: flex;
    width: 540px;
    min-height: 250px;
    margin-bottom: 90px;
    padding: 30px;
    background-color: #fff;
    border-radius: 15px;
    box-shadow: 0px 3px 6px 0.2px rgba(0, 0, 0, 0.15);
}

.movie__poster {
    position: absolute;
    top: -35px;
    left: 18px;
    margin-right: 30px;
    width: 170px;
    height: 255px;
    border-radius: 9px;
    box-shadow: 0px 3px 6px 0.2px rgba(0, 0, 0, 0.15);
}

.movie__data {
    margin-left: 190px;
}

.movie__title_group {
    margin-bottom: 5px;
}

.movie__title {
    display: inline;
    font-size: 24px;
    font-weight: 500;
    margin-right: 10px;
}

.movie__year {
    display: inline;
    font-weight: 500;
    color: #7d7d7d;
    flex-grow: 2;
}

.movie__genres {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 5px;
}

.genres__genre {
    margin-right: 10px;
    margin-bottom: 5px;
    padding: 0 6px;
    color: #646464;
    background-color: #eaeaea;
    border-radius: 4px;
}

.movie__rating {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
}

.movie__rating .star {
    display: inline-block;
    margin-right: 3px;
    width: 16px;
    height: 16px;
    background-image: url(/movie_app_2020/static/media/star.d59473d9.svg);
    background-repeat: no-repeat;
    background-size: cover;
}

.movie__rating .number {
    color: #646464;
}

.movie__summary {
    color: #646464;
    line-height: 1.5em;
}
* {
    margin: 0;
    box-sizing: border-box;
}

body {
    margin: 0 auto;
    padding: 40px;
    min-width: 640px;
    max-width: 1220px;
    font-family: 'Roboto', sans-serif;
    background-color: #f2f2f2;
}

#root {
    position: relative;
}

a {
    color: #000;
    text-decoration: none;
}

a:active {
    color: #000;
}

ul {
    padding: 0;
}

li {
    list-style: none;
}

/* loading */
@keyframes loading-spin-ani {
    100% {
        transform: rotate(360deg);
    }
}
@keyframes loading-circle-ani {
    0% {
        stroke-dashoffset: 157;

    }
    75% {
        stroke-dashoffset: -147;
        
    }
    100% {
        stroke-dashoffset: -157;
    }
}

.loader {
    position: absolute;
    top: 50vh;
    left: 50vw;
    transform: translate(-50%, -50%);
}

.loader__circle {
    width: 54px;
    height: 54px;
    animation: loading-spin-ani 3s infinite;
}

.loading-circle circle {
    stroke: #646464;
    stroke-width: 4;
    stroke-dasharray: 157;
    stroke-dashoffset: -157;
    fill: transparent;
    animation: loading-circle-ani 1s infinite;
}

.movies {
    display: flex;
    justify-content: space-between; 
    align-items: flex-start;
    flex-wrap: wrap;
}

.about {    
    padding: 30px;
    border-radius: 15px;
    box-shadow: 0px 3px 6px 0.2px rgba(0, 0, 0, 0.15);
    background-color: #fff;
}

.about__title {
    font-weight: 500;
    margin-bottom: 10px;
}

.about__link {
    text-decoration: underline;
}

.about__link:hover {
    color: #646464
}

@media screen and (max-width: 1210px) {
    #root {
        width: 540px;
        margin: 0 auto;
    }
}
