.nav_bar {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    margin-bottom: 80px;
}

.nav_bar a {
    position: relative;
    font-size: 18px;
    font-weight: 500;
}

.nav_bar a:first-child::before {
    content: "";
    position: absolute;
    top: 15px;
    left: -6px;
    display: block;
    width: 60px;
    height: 3px;
    background-color: #c4c4c4;
    z-index: -1;
}

.nav_bar a:last-child {
    font-size: 14px;
}

