.ditail__back_img {
    position: fixed;
    top: 100px;
    left: 0px;
    width: 100%;
    height: 350px;
    overflow: hidden;
    z-index: -1;
}

.ditail__back_img img{
    width: 100%;
    filter: brightness(80%);
}

.ditail.movie__poster {
    top: 110px;
    left: 0px;
    width: 220px;
    height: auto;
}

.movie__info_container {
    margin-top: 150px;
    margin-left: 270px;
}

.ditail__title {
    margin-bottom: 15px;
    font-size: 48px;
    color: #f2f2f2;
    font-weight: 500;
}

.ditail.movie__rating {
    margin-bottom: 15px;
}

.ditail.movie__rating .star {
    width: 25px;
    height: 25px;
}

.ditail.genres__genre {
    background-color: rgba(209, 209, 209, 0.7);
    font-size: 18px;
    color: #505050;
    font-weight: 500;
}

.movie__info {
    font-size: 16px;
    color: #929292;
    font-weight: 500;
}

.movie__ditail_info_container {
    position: absolute;
    top: 500px;
}

.movie__mini_title {
    margin-bottom: 20px;
    font-size: 24px;
    font-weight: 500;
}

.movie__ditail_info_container p {
    margin-bottom: 50px;
    line-height: 1.5em;
}

iframe {
    width: 540px;
    height: 295px;
}

@media screen and (max-width: 1210px) {
    .movie__info_container {
        margin-top: 20px;
    }
}